import { Plugin } from '@ali/4ever-cangjie';
import { isDisableNewLineGuide } from '../helpers';

function createOnQuery(): Plugin['onQuery'] {
  return function onQuery(event, controller, next) {
    const { type, args } = event;

    // 有 placeholder 展示则不显示 newLineGuide
    if (type === 'isNewLine') {
      const { node } = args[0];
      return !!node && next() && !isDisableNewLineGuide(node);
    }

    return next();
  };
}

export default createOnQuery;
